// Create the context
const UserDataContext = createContext();

// Create provider component
export const UserDataProvider = ({ children }) => {
  const [userData, setUserData] = useState(initialUserData);
  const [isLoaded, setIsLoaded] = useState(false);

  // Load data from localStorage on initial render
  useEffect(() => {
    const storedData = localStorage.getItem('frenchAppUserData');
    if (storedData) {
      try {
        setUserData(JSON.parse(storedData));
      } catch (error) {
        console.error('Error parsing user data from localStorage', error);
        // Use initial data if there's an error parsing
        setUserData(initialUserData);
      }
    } else {
      // No stored data, use initial data
      setUserData(initialUserData);
    }
    setIsLoaded(true);
  }, []);

  // Save data to localStorage whenever it changes
  useEffect(() => {
    if (isLoaded) {
      localStorage.setItem('frenchAppUserData', JSON.stringify(userData));
    }
  }, [userData, isLoaded]);

  // Update user progress
  const updateProgress = (courseType, lessonId, progressValue) => {
    setUserData(prevData => {
      // Create a deep copy of the courses
      const updatedCourses = JSON.parse(JSON.stringify(prevData.courses));
      
      // Find the lesson and update its progress
      const lesson = updatedCourses[courseType].find(l => l.id === lessonId);
      if (lesson) {
        // Track if this is the first time the lesson is being completed
        const wasCompleted = lesson.completed;
        
        lesson.progress = progressValue;
        
        // If progress is 100%, mark as completed
        if (progressValue === 100) {
          lesson.completed = true;
          
          // Unlock the next lesson if it exists
          const currentIndex = updatedCourses[courseType].findIndex(l => l.id === lessonId);
          if (currentIndex !== -1 && currentIndex + 1 < updatedCourses[courseType].length) {
            updatedCourses[courseType][currentIndex + 1].locked = false;
          }
        }
        
        // Update stats based on progress change
        const stats = { ...prevData.stats };
        // Only increment lessonsCompleted if this is the first time the lesson has been completed
        if (progressValue === 100 && !wasCompleted) {
          stats.lessonsCompleted += 1;
        }

        // Update daily goal progress
        const goals = { ...prevData.goals };
        goals.current = Math.min(goals.daily, goals.current + 5); // Add 5 points for making progress

        return {
          ...prevData,
          courses: updatedCourses,
          stats,
          goals,
        };
      }
      return prevData;
    });
  };

  // Update general user stats
  const updateStats = (statUpdates) => {
    setUserData(prevData => ({
      ...prevData,
      stats: {
        ...prevData.stats,
        ...statUpdates
      }
    }));
  };

  // Update activity for today
  const updateTodayActivity = (points) => {
    setUserData(prevData => {
      // Get a copy of the activity array
      const updatedActivity = [...prevData.activity];
      // Update the last element (today)
      updatedActivity[updatedActivity.length - 1] += points;
      
      return {
        ...prevData,
        activity: updatedActivity
      };
    });
  };

  // Update vocabulary progress
  const updateVocabularyProgress = (category, progressValue) => {
    setUserData(prevData => ({
      ...prevData,
      vocabularyProgress: {
        ...prevData.vocabularyProgress,
        [category]: progressValue
      }
    }));
  };

  // Update time distribution
  const updateTimeDistribution = (category, minutes) => {
    if (minutes <= 0) return; // Don't update if time is invalid
    
    setUserData(prevData => ({
      ...prevData,
      timeDistribution: {
        ...prevData.timeDistribution,
        [category]: prevData.timeDistribution[category] + minutes
      },
      stats: {
        ...prevData.stats,
        totalTime: prevData.stats.totalTime + minutes
      }
    }));
  };

  // Update accuracy stats
  const updateAccuracy = (newAccuracy) => {
    setUserData(prevData => {
      // Calculate weighted average if lessons have been completed
      let updatedAccuracy = newAccuracy;
      if (prevData.stats.lessonsCompleted > 0) {
        updatedAccuracy = Math.round(
          (prevData.stats.accuracy * prevData.stats.lessonsCompleted + newAccuracy) / 
          (prevData.stats.lessonsCompleted + 1)
        );
      }
      
      return {
        ...prevData,
        stats: {
          ...prevData.stats,
          accuracy: updatedAccuracy
        }
      };
    });
  };

  // Reset user data to initial state
  const resetUserData = () => {
    setUserData(initialUserData);
    localStorage.setItem('frenchAppUserData', JSON.stringify(initialUserData));
  };

  // Check daily streak and update
  const checkAndUpdateStreak = () => {
    // This would normally check the last login date and update the streak accordingly
    // For demonstration, we'll just increment the streak
    setUserData(prevData => ({
      ...prevData,
      stats: {
        ...prevData.stats,
        daysStreak: prevData.stats.daysStreak + 1
      }
    }));
  };

  // Context value
  const value = {
    userData,
    updateProgress,
    updateStats,
    updateTodayActivity,
    updateVocabularyProgress,
    updateTimeDistribution,
    updateAccuracy,
    resetUserData,
    checkAndUpdateStreak
  };

  return (
    <UserDataContext.Provider value={value}>
      {children}
    </UserDataContext.Provider>
  );
};

// Custom hook to use the context
export const useUserData = () => {
  const context = useContext(UserDataContext);
  if (context === undefined) {
    throw new Error('useUserData must be used within a UserDataProvider');
  }
  return context;
};

export default UserDataContext; 