import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useLocation } from 'react-router-dom';

const frenchQuotes = [
  "Les limites de ta langue sont les limites de ton monde.",
  "Apprendre une autre langue, c'est comme devenir une autre personne.",
  "La langue est la carte routière de la culture.",
  "Avec les langues, on est chez soi n'importe où.",
  "L'apprentissage n'est pas un sprint, c'est un marathon.",
  "L'erreur est humaine, persévérer est divin, apprendre du français, c'est intelligent !",
  "Oser, c'est gagner à moitié.",
  "Le français est une langue qui embellit le silence.",
  "Chaque langue ouvre une fenêtre sur le monde.",
  "La connaissance des langues est la porte de la sagesse.",
  "Rien n'est plus enrichissant que de comprendre une culture par sa langue.",
  "La motivation est ce qui vous fait démarrer. L'habitude est ce qui vous fait continuer.",
  "Le secret du succès est de commencer avant d'être prêt.",
  "Le français, c'est non seulement une langue de communication, mais aussi une langue de culture.",
  "Apprendre une langue, c'est ouvrir son cœur à un autre monde.",
  "Le chemin de mille lieues commence toujours par un premier pas.",
  "Un mot nouveau est comme un astre qui se lève.",
  "La grammaire, c'est la logique du langage.",
  "Pratiquez le français avec passion, apprenez avec plaisir.",
  "Parlez français, vivez le monde."
];

const QuoteDisplay = ({ className = '' }) => {
  const [currentQuoteIndex, setCurrentQuoteIndex] = useState(0);
  const [displayedText, setDisplayedText] = useState('');
  const [isTyping, setIsTyping] = useState(true);
  const [randomId, setRandomId] = useState(0);
  const location = useLocation();

  useEffect(() => {
    setRandomId(Math.random());
  }, []);

  useEffect(() => {
    const getRandomQuoteIndex = () => {
      const newIndex = Math.floor(Math.random() * frenchQuotes.length);
      return newIndex === currentQuoteIndex 
        ? (newIndex + 1) % frenchQuotes.length 
        : newIndex;
    };
    
    const newIndex = getRandomQuoteIndex();
    setCurrentQuoteIndex(newIndex);
    setDisplayedText('');
    setIsTyping(true);
  }, [location.pathname, randomId]);

  useEffect(() => {
    if (!isTyping) return;
    
    const currentQuote = frenchQuotes[currentQuoteIndex];
    
    if (displayedText.length < currentQuote.length) {
      const typingSpeed = 30;
      const timeout = setTimeout(() => {
        setDisplayedText(currentQuote.slice(0, displayedText.length + 1));
      }, typingSpeed);
      
      return () => clearTimeout(timeout);
    } else {
      setIsTyping(false);
    }
  }, [displayedText, currentQuoteIndex, isTyping]);

  return (
    <motion.h1 
      className={`text-3xl md:text-4xl font-extrabold text-center text-ios-gray-900 dark:text-white ${className}`}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6 }}
    >
      "{displayedText}
      {isTyping && (
        <span className="inline-block w-1 h-8 ml-1 bg-ios-blue dark:bg-white/70 animate-blink"></span>
      )}
      "
    </motion.h1>
  );
};

export default QuoteDisplay; 