import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { 
  ChevronDownIcon, 
  ChevronUpIcon, 
  ArrowTopRightOnSquareIcon,
  BookmarkIcon,
  ArrowLeftIcon
} from '@heroicons/react/24/outline';
import Card from '../components/Card';
import Badge from '../components/Badge';
import Button from '../components/Button';

const GrammarPage = () => {
  const [expandedSections, setExpandedSections] = useState({});
  
  // Toggle section expansion
  const toggleSection = (id) => {
    setExpandedSections(prev => ({
      ...prev,
      [id]: !prev[id]
    }));
  };
  
  // Grammar topics data
  const grammarTopics = [
    {
      id: 'basics',
      title: 'Basic Sentence Structure',
      level: 'Beginner',
      color: 'blue',
      description: 'Learn how to form basic French sentences with subjects, verbs, and objects.',
      sections: [
        {
          id: 'subject-verb',
          title: 'Subject-Verb Agreement',
          content: 'In French, verbs must agree with their subjects in person and number. For example, "Je parle" (I speak) but "Nous parlons" (We speak).',
          examples: [
            { french: 'Je mange une pomme.', english: 'I eat an apple.' },
            { french: 'Tu manges une pomme.', english: 'You eat an apple.' },
            { french: 'Il/Elle mange une pomme.', english: 'He/She eats an apple.' },
          ],
        },
        {
          id: 'word-order',
          title: 'Word Order',
          content: 'French generally follows Subject-Verb-Object order, similar to English. However, there are important differences with adjective placement.',
          examples: [
            { french: 'J\'aime le café.', english: 'I like coffee.' },
            { french: 'Elle regarde la télévision.', english: 'She watches television.' },
          ],
        },
      ],
    },
    {
      id: 'articles',
      title: 'Articles',
      level: 'Beginner',
      color: 'green',
      description: 'Learn about definite, indefinite and partitive articles in French.',
      sections: [
        {
          id: 'definite-articles',
          title: 'Definite Articles (le, la, les)',
          content: 'Definite articles are used to talk about specific items. "Le" is used for masculine nouns, "la" for feminine nouns, and "les" for plural nouns.',
          examples: [
            { french: 'Le livre est sur la table.', english: 'The book is on the table.' },
            { french: 'J\'aime la musique.', english: 'I like music.' },
            { french: 'Les chats dorment.', english: 'The cats are sleeping.' },
          ],
        },
        {
          id: 'indefinite-articles',
          title: 'Indefinite Articles (un, une, des)',
          content: 'Indefinite articles are used to talk about non-specific items. "Un" is used for masculine nouns, "une" for feminine nouns, and "des" for plural nouns.',
          examples: [
            { french: 'J\'ai un stylo.', english: 'I have a pen.' },
            { french: 'Elle a une idée.', english: 'She has an idea.' },
            { french: 'Il y a des livres sur la table.', english: 'There are some books on the table.' },
          ],
        },
      ],
    },
    {
      id: 'present-tense',
      title: 'Present Tense',
      level: 'Beginner',
      color: 'orange',
      description: 'Learn how to conjugate and use verbs in the present tense.',
      sections: [
        {
          id: 'regular-er',
          title: 'Regular -er Verbs',
          content: 'Regular -er verbs follow a consistent pattern. Remove the -er ending and add the appropriate ending for the subject.',
          examples: [
            { french: 'Je parle français.', english: 'I speak French.' },
            { french: 'Tu parles français.', english: 'You speak French.' },
            { french: 'Il/Elle parle français.', english: 'He/She speaks French.' },
            { french: 'Nous parlons français.', english: 'We speak French.' },
            { french: 'Vous parlez français.', english: 'You (plural/formal) speak French.' },
            { french: 'Ils/Elles parlent français.', english: 'They speak French.' },
          ],
        },
        {
          id: 'regular-ir',
          title: 'Regular -ir Verbs',
          content: 'Regular -ir verbs follow a different pattern than -er verbs. Remove the -ir ending and add the appropriate ending for the subject.',
          examples: [
            { french: 'Je finis mon travail.', english: 'I finish my work.' },
            { french: 'Tu finis ton travail.', english: 'You finish your work.' },
            { french: 'Il/Elle finit son travail.', english: 'He/She finishes his/her work.' },
            { french: 'Nous finissons notre travail.', english: 'We finish our work.' },
            { french: 'Vous finissez votre travail.', english: 'You (plural/formal) finish your work.' },
            { french: 'Ils/Elles finissent leur travail.', english: 'They finish their work.' },
          ],
        },
      ],
    },
    {
      id: 'adjectives',
      title: 'Adjectives',
      level: 'Beginner',
      color: 'purple',
      description: 'Learn how adjectives agree with nouns in gender and number, and how they are positioned in sentences.',
      sections: [
        {
          id: 'agreement',
          title: 'Agreement with Nouns',
          content: 'In French, adjectives must agree with the nouns they modify in both gender and number.',
          examples: [
            { french: 'Un petit garçon', english: 'A small boy' },
            { french: 'Une petite fille', english: 'A small girl' },
            { french: 'Des petits garçons', english: 'Small boys' },
            { french: 'Des petites filles', english: 'Small girls' },
          ],
        },
        {
          id: 'position',
          title: 'Position of Adjectives',
          content: 'Unlike English, most French adjectives are placed after the noun. However, some common adjectives come before the noun.',
          examples: [
            { french: 'Une maison blanche', english: 'A white house (adjective after)' },
            { french: 'Un grand arbre', english: 'A tall tree (adjective before)' },
          ],
        },
      ],
    },
  ];
  
  // Get category-specific colors
  const getTopicStyles = (color) => {
    switch(color) {
      case 'blue':
        return {
          widgetVariant: 'widgetBlue',
          accent: 'bg-[#0A58CA] dark:bg-[#0A84FF]',
          button: 'border-[#0A58CA] text-[#0A58CA] dark:border-[#0A84FF] dark:text-[#0A84FF]',
          badge: 'filled-blue',
          headerBg: 'bg-[#D1E5FF]/50 dark:bg-[#0A58CA]/30'
        };
      case 'green':
        return {
          widgetVariant: 'widgetGreen',
          accent: 'bg-[#148F47] dark:bg-[#34C759]',
          button: 'border-[#148F47] text-[#148F47] dark:border-[#34C759] dark:text-[#34C759]',
          badge: 'filled-green',
          headerBg: 'bg-[#D4F7DF]/50 dark:bg-[#148F47]/30'
        };
      case 'orange':
        return {
          widgetVariant: 'widgetOrange',
          accent: 'bg-[#CC5500] dark:bg-[#FF9500]',
          button: 'border-[#CC5500] text-[#CC5500] dark:border-[#FF9500] dark:text-[#FF9500]',
          badge: 'filled-orange',
          headerBg: 'bg-[#FFE2BD]/50 dark:bg-[#CC5500]/30'
        };
      case 'purple':
        return {
          widgetVariant: 'widgetPurple',
          accent: 'bg-[#7C3AED] dark:bg-[#AF52DE]',
          button: 'border-[#7C3AED] text-[#7C3AED] dark:border-[#AF52DE] dark:text-[#AF52DE]',
          badge: 'filled-purple',
          headerBg: 'bg-[#E7D6FF]/50 dark:bg-[#7C3AED]/30'
        };
      default:
        return {
          widgetVariant: 'widgetBlue',
          accent: 'bg-[#0A58CA] dark:bg-[#0A84FF]',
          button: 'border-[#0A58CA] text-[#0A58CA] dark:border-[#0A84FF] dark:text-[#0A84FF]',
          badge: 'filled-blue',
          headerBg: 'bg-[#D1E5FF]/50 dark:bg-[#0A58CA]/30'
        };
    }
  };
  
  // Animation variants
  const container = {
    hidden: { opacity: 0 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };
  
  const item = {
    hidden: { opacity: 0, y: 20 },
    show: { opacity: 1, y: 0, transition: { duration: 0.4 } }
  };
  
  return (
    <div className="py-8">
      {/* Header with Back button */}
      <div className="flex justify-between items-center mb-8">
        <div className="flex items-center">
          <Link to="/" className="mr-4">
            <Button variant="icon" className="bg-white/80 dark:bg-dark-200/80 shadow-sm backdrop-blur-sm">
              <ArrowLeftIcon className="h-6 w-6 text-ios-gray-900 dark:text-white" />
            </Button>
          </Link>
          <h1 className="text-3xl font-bold text-ios-gray-900 dark:text-white">Grammar</h1>
        </div>
      </div>
      
      <motion.div
        variants={container}
        initial="hidden"
        animate="show"
        className="space-y-6"
      >
        {grammarTopics.map((topic) => {
          const topicStyles = getTopicStyles(topic.color);
          return (
          <motion.div key={topic.id} variants={item}>
            <Card variant={topicStyles.widgetVariant}>
              <div className="mb-4">
                <div className="flex justify-between items-start">
                  <div>
                    <div className="flex items-center mb-2">
                      <h2 className="text-xl font-bold text-ios-gray-900 dark:text-white mr-3">{topic.title}</h2>
                      <Badge variant={topicStyles.badge}>{topic.level}</Badge>
                    </div>
                    <p className="text-ios-gray-700 dark:text-white/80">{topic.description}</p>
                  </div>
                  
                  <div className="flex space-x-2">
                    <Button 
                      variant="outline" 
                      className={`p-2 border ${topicStyles.button}`}
                      title="Bookmark this topic"
                    >
                      <BookmarkIcon className="h-5 w-5" />
                    </Button>
                    <Link to={`/lesson/${topic.id}`}>
                      <Button className={`p-2 text-white ${topicStyles.accent}`} title="Open as lesson">
                        <ArrowTopRightOnSquareIcon className="h-5 w-5" />
                      </Button>
                    </Link>
                  </div>
                </div>
              </div>
              
              <div className="space-y-4">
                {topic.sections.map((section) => (
                  <div 
                    key={section.id} 
                    className="border border-white/20 dark:border-white/10 rounded-xl overflow-hidden shadow-sm"
                  >
                    <button
                      className={`w-full flex justify-between items-center p-4 text-left ${topicStyles.headerBg} hover:brightness-105 transition-colors`}
                      onClick={() => toggleSection(`${topic.id}-${section.id}`)}
                    >
                      <h3 className="text-lg font-semibold text-ios-gray-900 dark:text-white">{section.title}</h3>
                      {expandedSections[`${topic.id}-${section.id}`] ? (
                        <ChevronUpIcon className="h-5 w-5 text-ios-gray-600 dark:text-white/70" />
                      ) : (
                        <ChevronDownIcon className="h-5 w-5 text-ios-gray-600 dark:text-white/70" />
                      )}
                    </button>
                    
                    {expandedSections[`${topic.id}-${section.id}`] && (
                      <div className="p-4 bg-white/70 dark:bg-dark-200/70 backdrop-blur-sm">
                        <p className="text-ios-gray-700 dark:text-white/80 mb-4">{section.content}</p>
                        
                        {section.examples && (
                          <div>
                            <h4 className="text-md font-medium text-ios-gray-900 dark:text-white mb-2">Examples:</h4>
                            <div className="space-y-2">
                              {section.examples.map((example, idx) => (
                                <div key={idx} className="grid grid-cols-2 gap-2 p-2 bg-white/50 dark:bg-dark-300/50 backdrop-blur-sm rounded-lg">
                                  <div className="text-[#0A58CA] dark:text-[#0A84FF] font-medium">{example.french}</div>
                                  <div className="text-ios-gray-700 dark:text-white/80">{example.english}</div>
                                </div>
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </Card>
          </motion.div>
        )})}
      </motion.div>
    </div>
  );
};

export default GrammarPage; 